import { Fragment } from "react";
import { Alert } from "@material-tailwind/react";

export default function Colors({ showAlert, setShowAlert }) {
  return (
    <Fragment>
      <Alert
        show={showAlert}
        animate={{
          mount: { y: 0 },
          unmount: { y: 100 },
        }}
        dismissible={{
          onClose: () => setShowAlert(false),
        }}
      >
        Added To Cart.
      </Alert>
    </Fragment>
  );
}
